import { defineStore } from 'pinia';
import Swal from 'sweetalert2';
import toastStore from './toastStore';

const toast = toastStore();

export default defineStore('favorite', {
  state: () => ({
    favorited: JSON.parse(localStorage.getItem('favorite')) || [],
  }),
  actions: {
    // 
    delFav(product) {
      const array = [];
      this.favorited.forEach((item) => {
        array.push(item.id);
      });
      this.favorited.splice(array.indexOf(product.id), 1);
      localStorage.setItem('favorite', JSON.stringify(this.favorited));

      const messageTitle = '';
      toast.pushMessage({
        style: 'danger',
        title: messageTitle,
        content: `${product.title}`,
      });
    },
    // 
    confirmClearAll() {
      Swal.fire({
        text: '',
        icon: 'warning',
        cancelButtonText: '',
        confirmButtonText: '',
        showCancelButton: true,
      }).then((res) => {
        if (res.isConfirmed) {
          this.clearAllFav();
        }
      });
    },
    // 
    clearAllFav() {
      this.favorited = [];
      localStorage.setItem('favorite', JSON.stringify(this.favorited));

      const messageTitle = '';
      toast.pushMessage({
        style: 'danger',
        title: messageTitle,
        content: '',
      });
    },
    // 
    setFav(product) {
      if (this.favorited.length) {
        const array = [];
        this.favorited.forEach((item) => {
          array.push(item.id);
        });
        if (array.includes(product.id)) {
          this.favorited.splice(array.indexOf(product.id), 1);
        } else {
          this.favorited.push(product);
        }
      } else {
        this.favorited.push(product);
      }
      localStorage.setItem('favorite', JSON.stringify(this.favorited));
    },
    // 
    favState(product) {
      const array = [];
      this.favorited.forEach((item) => {
        array.push(item.id);
      });

      if (array.includes(product.id)) {
        return 'fa-solid fa-heart';
      }
      return 'fa-regular fa-heart';
    },
  },
});
