import { defineStore } from 'pinia';
import axios from 'axios';
import router from '@/router';
import Swal from 'sweetalert2';
import toastStore from './toastStore';

const toast = toastStore();

export default defineStore('cart', {
  state: () => ({
    cart: {},
    cartLength: '',
    couponWarning: '',
    btnLoading: '',
    isLoading: false,
  }),
  actions: {
    // 
    getCart() {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`;

      axios.get(url)
        .then((res) => {
          this.isLoading = false;
          this.cart = res.data.data;
          this.cartLength = this.cart.carts.length;
        })
        .catch(() => {
          this.isLoading = false;
          toast.pushMessage({
            style: 'danger',
            title: '',
          });
        });
    },
    // 
    updateCart(item, num) {
      this.isLoading = true;
      let quantity = num;
      if (quantity > 20) { // 2020
        quantity = 20;
      } else if (quantity < 1) { // 11
        quantity = 1;
      }

      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart/${item.id}`;
      const cart = {
        product_id: item.product_id,
        qty: quantity,
      };

      axios.put(url, { data: cart })
        .then(() => {
          this.getCart();
        })
        .catch(() => {
          toast.pushMessage({
            style: 'danger',
            title: '',
          });
        });
    },
    // 
    addtoCart(item, quantity = 1) {
      this.btnLoading = item.id;

      // 
      let existingProduct;
      if (!this.cart.carts) {
        this.getCart();
      } else {
        // 
        existingProduct = this.cart.carts.filter((i) => i.product_id === item.id);
      }

      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`;
      const cart = {
        product_id: item.id,
        qty: quantity,
      };

      axios.post(url, { data: cart })
        .then((res) => {
          this.btnLoading = '';
          const { title } = res.data.data.product;
          // 20updateCart20
          if (res.data.data.qty > 20) {
            const messageTitle = '';
            toast.pushMessage({
              style: 'warning',
              title: messageTitle,
              content: `20${title}`,
            });
            this.updateCart(existingProduct[0], 20);
          } else {
            const messageTitle = '';
            toast.pushMessage({
              style: 'success',
              title: messageTitle,
              content: `${title}`,
            });
            this.getCart();
          }
        })
        .catch(() => {
          this.btnLoading = '';
          toast.pushMessage({
            style: 'danger',
            title: '',
          });
        });
    },
    // 
    deleteCartItem(item) {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart/${item.id}`;

      axios.delete(url)
        .then(() => {
          this.getCart();
          const messageTitle = '';
          toast.pushMessage({
            style: 'danger',
            title: messageTitle,
            content: `${item.product.title}`,
          });
        })
        .catch(() => {
          this.isLoading = false;
          toast.pushMessage({
            style: 'danger',
            title: '',
          });
        });
    },
    // 
    confirmDeleteAll() {
      Swal.fire({
        text: '',
        icon: 'warning',
        cancelButtonText: '',
        confirmButtonText: '',
        showCancelButton: true,
      }).then((res) => {
        if (res.isConfirmed) {
          this.deleteAll();
        }
      });
    },
    // 
    deleteAll() {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/carts`;

      axios.delete(url)
        .then(() => {
          this.getCart();
          const messageTitle = '';
          toast.pushMessage({
            style: 'danger',
            title: messageTitle,
            content: '',
          });
        })
        .catch(() => {
          this.isLoading = false;
          toast.pushMessage({
            style: 'danger',
            title: '',
          });
        });
    },
    // 
    addCouponCode(couponCode) {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/coupon`;
      const dataCode = { code: couponCode };

      axios.post(url, { data: dataCode })
        .then((res) => {
          if (res.data.success) {
            this.getCart();
            this.couponWarning = '';
          } else {
            this.isLoading = false;
            this.couponWarning = '';
          }
        })
        .catch(() => {
          this.isLoading = false;
          toast.pushMessage({
            style: 'danger',
            title: '',
          });
        });
    },
    // 
    returnPage() {
      router.go(-1);
    },
    // 
    returnIndex() {
      router.push('/index');
    },
    // 
    gotoShop() {
      router.push('/productsList');
    },
  },
});
