import { defineStore } from 'pinia';
import axios from 'axios';
import router from '@/router';
import toastStore from './toastStore';

const toast = toastStore();

export default defineStore('product', {
  state: () => ({
    product: {},
    preferProducts: [],
    products: [],
    pagination: {},
    ProductsArr: [],
    renderProducts: [],
    newData: [],
    tempCategory: '',
    isLoading: false,
  }),
  actions: {
    // 
    getProduct(id) {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/product/${id}`;

      axios.get(url)
        .then((res) => {
          this.isLoading = false;
          this.product = res.data.product;
        })
        .catch(() => {
          this.isLoading = false;
          toast.pushMessage({
            style: 'danger',
            title: '',
          });
        });
    },
    // 
    getPreferProducts() {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products`;

      axios.get(url)
        .then((res) => {
          this.preferProducts = res.data.products;
        })
        .catch(() => {
          this.isLoading = false;
          toast.pushMessage({
            style: 'danger',
            title: '',
          });
        });
    },
    // 
    getProducts(page = 1) {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products?page=${page}`;

      axios.get(url)
        .then((res) => {
          this.products = res.data.products;
          this.pagination = res.data.pagination;
          this.pagination.category = this.tempCategory || '';
          // 
          this.products.forEach((item) => {
            this.ProductsArr.push(item);
          });
          this.renderProducts = [...this.ProductsArr];
          this.renderCategory(page);
        })
        .catch(() => {
          this.isLoading = false;
          toast.pushMessage({
            style: 'danger',
            title: '',
          });
        });
    },
    // 
    renderCategory(page) {
      // 
      if (page < this.pagination.total_pages) {
        this.getProducts(page + 1);
      } else {
        this.isLoading = false;
        // 
        if (this.pagination.category !== '') {
          this.renderProducts = this.ProductsArr.filter((item) => (
            item.category === this.pagination.category
          ));
        }
        // 
        this.newData = [];
        this.renderProducts.forEach((item, i) => {
          if (i % 10 === 0) {
            this.newData.push([]);
          }
          const pagenum = parseInt(i / 10, 10);
          this.newData[pagenum].push(item);
        });

        this.pagination.current_page = 1;
        this.pagination.total_pages = this.newData.length;
        this.sortProducts = this.renderProducts;
        this.ProductsArr = [];
      }
    },
    // 
    sort(price) {
      if (price === 'low') {
        this.renderProducts.sort((a, b) => a.price - b.price);
      } else {
        this.renderProducts.sort((a, b) => b.price - a.price);
      }
      this.ProductsArr = this.renderProducts;
      this.renderCategory();
    },
    updateCategory(category) {
      this.tempCategory = category;
    },
    // 
    gotoProduct(id) {
      router.push(`/product/${id}`);
    },
    getPage(page) {
      this.pagination.current_page = page;
      window.scrollTo(0, 0);
    },
  },
});
